<template>
  <div>
    <div class="fasilitas-detail">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <base-header v-if="isAdmin" class="pb-8" type="primary">
        <div class="row">
          <div class="col-lg-12 py-2 col-5 text-right">
            <base-button size="sm" type="danger" @click="deleteData"
              >Hapus</base-button
            >
            <base-button size="sm" type="neutral" @click="openEditForm"
              >Ubah</base-button
            >
          </div>
        </div>
      </base-header>
      <base-header
        v-if="!isAdmin"
        class="pt-4 pb-8"
        type="primary"
      ></base-header>
      <div class="container-fluid mt--8">
        <div class="row">
          <div class="col-12">
            <div class="card">
              <!-- Card image -->
              <img
                class="card-img-top"
                :src="fasilitas.cover[0].path"
                alt="Cover Image"
              />
              <!-- Card body -->
              <div class="card-body">
                <h5 class="h2 card-title mb-0">{{ fasilitas.name }}</h5>
                <small class="text-muted"
                  >Berlokasi di {{ getUnitName(fasilitas.unit) }} dan direkam
                  pada {{ unixTS2DBYHMS(fasilitas.timestamp / 1000) }}</small
                >
                <p>{{ fasilitas.overview }}</p>
                <div class="container mb-4">
                  <div class="row justify-content-center">
                    <div class="col-12 rounded overflow-hidden p-0">
                      <el-carousel height="480px">
                        <template v-for="image in fasilitas.content.images">
                          <el-carousel-item class="carousel-item">
                            <img
                              class="d-block card-img-content"
                              :src="image.path"
                              alt="Image"
                            />
                          </el-carousel-item>
                        </template>
                      </el-carousel>
                    </div>
                  </div>
                </div>
                <div v-html="fasilitas.content.text"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Cabang from "../cabang";
import Detail from "./detail";
import { Carousel, CarouselItem } from "element-ui";
import Axios from "axios";
import Swal from "sweetalert2";
import "sweetalert2/dist/sweetalert2.css";

export default {
  components: {
    [Carousel.name]: Carousel,
    [CarouselItem.name]: CarouselItem,
  },
  data() {
    return {
      api: this.$store.getters.getAPI,
      dummy: {
        isDummy: true,
      },
      cabang: Cabang,
      fasilitas: {
        id: null,
        cover: [
          {
            path: null,
          },
        ],
        name: null,
        unit: null,
        timestamp: null,
        overview: null,
        content: {
          images: [],
          text: null,
        },
      },
    };
  },
  computed: {
    isAdmin() {
      return this.$store.getters.isAdmin;
    },
  },
  methods: {
    deleteData() {
      Swal.fire({
        title: `Hapus Data`,
        text: "Apakah anda yakin untuk menghapus data?",
        showCancelButton: true,
        icon: "question",
      }).then((result) => {
        if (result.isConfirmed) {
          Axios.delete(
            this.api.url + this.api.fs.deleteFS + this.$route.params.id
          )
            .then((response) => {
              Swal.fire("Data terhapus", "", "success").then(() => {
                this.$router.push("/fasilitas/overview");
              });
            })
            .catch((error) => {
              Swal.fire("Data tidak terhapus", "ERROR: " + error, "error");
            });
        } else {
          Swal.fire("Data tidak terhapus", "", "error");
        }
      });
    },
    openEditForm() {
      this.$store.commit("setUbahFasilitas", this.fasilitas);
      this.$router.push("/fasilitas/form/ubah");
    },
    getFasilitas(query) {
      switch (query) {
        case "dummy":
          this.fasilitas = Detail;
          break;

        default:
          Axios.get(this.api.url + this.api.fs.getFS + this.$route.params.id)
            .then((response) => {
              if (this.$store.getters.isDebugging) {
                console.log(
                  "DEBUG: Success to get data. Data: ",
                  response.data
                );
                this.fasilitas = {
                  id: response.data[0].FC_ID,
                  timestamp: response.data[0].TIMESTAMP,
                  unit: response.data[0].UNITKERJA_ID,
                  type: response.data[0].TYPE,
                  name: response.data[0].NAME,
                  overview: response.data[0].OVERVIEW,
                  cover: JSON.parse(response.data[0].COVER),
                  content: JSON.parse(response.data[0].CONTENT),
                };
              }
            })
            .catch((error) => {
              if (this.$store.getters.isDebugging) {
                console.log("ERROR: Failed to get data. ", error);
                // this.$router.push("form/tambah");
              }
            });
          break;
      }
    },
    getUnitName(id) {
      for (let index = 0; index < this.cabang.length; index++) {
        if (this.cabang[index].value === id) {
          return this.cabang[index].label;
        }
      }
    },
    unixTS2DBYHMS(UNIX_timestamp) {
      var a = new Date(UNIX_timestamp * 1000);
      var months = [
        "Januari",
        "Februari",
        "Maret",
        "April",
        "Mei",
        "Juni",
        "Juli",
        "Agustus",
        "September",
        "Oktober",
        "November",
        "Desember",
      ];
      var year = a.getFullYear();
      var month = months[a.getMonth()];
      var nmonth = a.getMonth() + 1;
      var date = a.getDate();
      var hour = a.getHours();
      var min = a.getMinutes();
      var sec = a.getSeconds();
      var time =
        date + " " + month + " " + year + " " + hour + ":" + min + ":" + sec;
      var xtime = date + "/" + nmonth + "/" + year;
      return time;
    },
  },
  created() {
    this.getFasilitas();
  },
  watch: {
    $route(to, from) {
      this.getFasilitas();
    },
  },
};
</script>

<style lang="scss" scoped>
.rounded-container {
  border-radius: 0.5rem;
  overflow: hidden;
  padding: 0;
}
.card-img-top {
  height: 360px;
  object-fit: cover;
}
.card-img-content {
  object-fit: cover;
  width: 100%;
}
</style>

<style lang="scss">
.fasilitas-detail {
  .el-carousel {
    button {
      &:focus {
        outline: unset;
      }
    }
  }
}
</style>